export default {
	data() {
		return {
			degL: -45, 		//leftProgress旋转角度
			degR: 45,		//rightProgress旋转角度
			curScore: 60
		}
	},
	onLoad(e) {
		this.sliderchange(this.curScore);
	},
	methods: {
		//计算旋转角度
		sliderchange: function(score) {
			var deg = score / 100 * 360;
			if (deg <= 180) {
				this.degL = -45;
				this.degR = 45 + deg;
			} else {
				this.degL = -45 + (deg - 180);
				this.degR = 45 + 180;
			}
		}
	}
}
